<template>
  <div>
    <el-table
      class="table-responsive align-items-center table-flush"
      header-row-class-name="thead-light"
      :data="data"
    >
      <!-- Name |  email(optional)  |  0-6 months for free | expiration date -->
      <el-table-column label="Name" min-width="140px" prop="name" sortable>
        <template v-slot="{ row }">
          <input
            type="text"
            style="
              padding: 0 !important;
              background-color: transparent !important;
            "
            class="form-control form-control-muted tk-border-bottom tk-font-color-black1"
            @focus.prevent="name = row.name"
            @blur.prevent="saveName(row)"
            :placeholder="initName(row.name)"
            v-model="row.name"
          />
        </template>
      </el-table-column>

      <el-table-column label="Email" min-width="140px" prop="email" sortable>
        <template v-slot="{ row }">
          <input
            type="email"
            style="
              padding: 0 !important;
              background-color: transparent !important;
            "
            class="form-control form-control-muted tk-border-bottom tk-font-color-black1"
            @focus.prevent="email = row.email"
            @blur.prevent="saveEmail(row)"
            :placeholder="initEmail(row.email)"
            v-model="row.email"
          />
        </template>
      </el-table-column>

      <el-table-column
        label="Free months"
        min-width="140px"
        prop="time"
        sortable
      >
        <template v-slot="{ row }">
          <div>{{ initTime(row.time) }}</div>
        </template>
      </el-table-column>

      <el-table-column
        label="Expiration Date"
        min-width="120px"
        prop="expiration"
        sortable
      >
        <template v-slot="{ row }">
          <div
            :class="{
              'tk-font-color-lightGray':
                row.expireIn * 1000 < new Date().getTime() &&
                row.expireIn !== 0,
            }"
          >
            {{ initExpiration(row.expireIn) }}
          </div>
        </template>
      </el-table-column>

      <el-table-column
        label="Link"
        min-width="140px"
        prop="expiration"
        sortable
      >
        <template v-slot="{ row }">
          <span>{{ initLink(row.link) }}</span>
          <span
            v-clipboard:copy="row.link"
            v-clipboard:error="onCopyError"
            v-clipboard:success="onCopySuccess"
            class="table-action"
            data-toggle="tooltip"
            style="margin-left: 10px !important"
            v-if="!isNull(row.link)"
          >
            <i class="fas fa-copy"></i>
          </span>
        </template>
      </el-table-column>

      <!-- <el-table-column width="72px" label="Stats">
        <div
          slot-scope="{ row }"
          class="table-actions tk-dynamic-link-opertation"
        >
          <el-dropdown trigger="click" class="dropdown">
            <span class="tk-font-color-lightGray" style="margin-left: 10px">
              <i class="ni ni-chart-bar-32"></i>
            </span>
            <el-dropdown-menu
              class="dropdown-menu dropdown-menu-arrow show"
              slot="dropdown"
              style="width: 400px; height: 400px"
            >
            </el-dropdown-menu>
          </el-dropdown> -->

          <!-- <template>
            <el-tooltip content="Enable" placement="top" v-if="isExpired(row)">
              <span
                @click.prevent="onEnable(row)"
                class="table-action"
                data-toggle="tooltip"
                data-original-title="Enable"
                style="margin-left: 1rem !important"
              >
                <i class="fas fa-play"></i>
              </span>
            </el-tooltip>
            <el-tooltip content="Archive" placement="top" v-else>
              <span
                @click.prevent="onArchive(row)"
                class="table-action"
                data-toggle="tooltip"
                data-original-title="Archive"
                style="margin-left: 1rem !important"
              >
                <i class="fas fa-inbox"></i>
              </span>
            </el-tooltip>
          </template> -->
        <!-- </div>
      </el-table-column> -->
    </el-table>

    <modal :show.sync="showArchive">
      <template slot="header">
        <h5 class="modal-title">Archive Coupon</h5>
      </template>
      <div class="tk-popup-content">
        <span>Are you sure to archive this coupon?</span>
      </div>
      <template slot="footer">
        <base-button
          type="secondary"
          @click.prevent="showArchive = false"
          class="tk-font-color-lightGray tk-border-none"
          >Close</base-button
        >
        <!-- <base-button
          type="main"
          @click.prevent="archiveCoupon"
          class="tk-font-color-white tk-border-none"
          >Sure</base-button
        > -->
        <button-with-loading
          style="margin: 0 0 0 1rem"
          text="Sure"
          @start="archiveCoupon"
          :type="'main'"
          :complete="isButtonLoadingComplete"
        ></button-with-loading>
      </template>
    </modal>

    <modal :show.sync="showEnable">
      <template slot="header">
        <h5 class="modal-title">Enable Coupon</h5>
      </template>
      <div class="tk-popup-content">
        <!-- expiration -->
        <span>
          <div class="form-group card-wrapper tk-px-margin-bottom-px0">
            <div class="card tk-px-margin-bottom-px16">
              <div class="form-control" style="height: auto">
                <span
                  class="tk-layout-inline-block tk-font-color-black1"
                  style="width: calc(100% - 56px)"
                >
                  <span v-if="!expirationOpen">Expiration</span>
                  <template v-else>
                    <span>Expiration date</span>
                    <flat-picker
                      :config="{ allowInput: true }"
                      class="form-control form-control-muted tk-bg-color-gray-100 datepicker tk-layout-inline-block tk-px-width-px100 tk-px-margin-left-px10 tk-px-height-px36"
                      v-model="expiration"
                    >
                    </flat-picker>
                  </template>
                </span>
                <base-switch
                  class="mr-1 tk-layout-inline-block"
                  v-model="expirationOpen"
                  on-text="On"
                  off-text="Off"
                  type="main"
                ></base-switch>
              </div>
            </div>
          </div>
        </span>
      </div>
      <template slot="footer">
        <base-button
          type="secondary"
          @click.prevent="showEnable = false"
          class="tk-font-color-lightGray tk-border-none"
          >Close</base-button
        >
        <button-with-loading
          style="margin: 0 0 0 1rem"
          text="Enable"
          @start="enableCoupon"
          :type="'main'"
          :complete="isButtonLoadingComplete"
        ></button-with-loading>
      </template>
    </modal>
  </div>
</template>

<script>
import collections from "@/services/collections";
import flatPicker from "vue-flatpickr-component";
import buttonWithLoading from "@/tkViews/components/layout/ButtonWithLoading";
// import { google } from "googleapis"
import {
  Table,
  TableColumn,
  DropdownMenu,
  DropdownItem,
  Dropdown,
  Tooltip,
} from "element-ui";
export default {
  props: {
    salesManParams: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  components: {
    [Tooltip.name]: Tooltip,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    [Dropdown.name]: Dropdown,
    [DropdownItem.name]: DropdownItem,
    [DropdownMenu.name]: DropdownMenu,
    flatPicker,
    buttonWithLoading,
  },
  data() {
    return {
      data: [],
      sales: [],
      name: "",
      email: "",

      showArchive: false,
      showEnable: false,
      row: null,
      expiration: "",
      expirationOpen: false,
      isButtonLoadingComplete: false,

      accessToken: "",
      google: google
    };
  },
  async created() {
    let self = this;

    self.data = self.salesManParams;

    // self.getAccessToken();
  },
  watch: {
    salesManParams: {
      handler(newVal, oldval) {
        this.data = newVal;
      },
      deep: true,
    },
    expirationOpen(newVal, oldVal) {
      if (newVal) {
        this.expiration = this.$tools.formatYYMMDD(new Date().getTime());
      } else {
        this.expiration = "";
      }
    },
  },
  methods: {
    getAccessToken() {
      let self = this;
      let google = self.google

      const serviceAccount = require("@/services/serviceAccountKey.json");

      console.log(self.google, serviceAccount)

      var jwtClient = new google.auth.JWT(
        serviceAccount.client_email,
        null,
        serviceAccount.private_key,
        ["https://www.googleapis.com/auth/firebase"]
      );

      jwtClient.authorize((error, tokens) => {
        if (error) {
          console.log("Error making request to generate access token:", error);
        } else if (tokens.access_token === null) {
          console.log(
            "Provided service account does not have permission to generate access tokens"
          );
        } else {
          self.accessToken = tokens.access_token;
          console.log(self.accessToken)
        }
      });

      // let func = self.$firebase.functions().httpsCallable("getAccessToken");
      // await func()
      //   .then((result) => {
      //     console.log("result: ", result)
      //   })
    },
    initLink(link) {
      let str1 = link.substring(0, 15),
        str2 = link.substring(link.length - 4, link.length);
      return str1 + "..." + str2;
    },
    isExpired(row) {
      let timestamp =
        row.expireIn === "" || row.expireIn === 0
          ? new Date().getTime()
          : row.expireIn * 1000;
      if (timestamp < new Date().getTime()) {
        // 过期
        return true;
      } else {
        // 未过期
        return false;
      }
    },
    saveName(row) {
      if (!this.$tools.isNull(row.name)) {
        collections.salesManParams
          .doc(row.id)
          .update({
            name: row.name,
          })
          .then(() => {
            console.log("name 修改成功");
          });
      } else {
        row.name = this.name;
      }
    },
    saveEmail(row) {
      if (!this.$tools.isNull(row.email)) {
        if (this.$tools.isEmail(row.email)) {
          collections.salesMan
            .doc(row.salesManId)
            .set({
              salesManId: row.salesManId,
              email: row.email,
            })
            .then(() => {
              console.log("email 修改成功");
            });
        } else {
          this.$notify({
            message: "Wrong email, please check it!",
            type: "error",
          });
          row.email = this.email;
        }
      } else {
        row.email = this.email;
      }
    },
    onStats(row) {
      alert("Stats");
    },
    onArchive(row) {
      // alert("Archive");
      this.showArchive = true;
      this.row = row;
    },
    archiveCoupon() {
      let self = this;
      let expireIn = parseInt(
        (new Date().getTime() - 24 * 60 * 60 * 1000) / 1000
      );

      collections.salesManParams
        .doc(self.row.id)
        .update({
          expireIn: expireIn,
        })
        .then(() => {
          self.isButtonLoadingComplete = true;
          self.data.some((item) => {
            if (item.id === self.row.id) {
              item.expireIn = expireIn;
            }
          });
          self.row = null;
          self.showArchive = false;
        });

      self.isButtonLoadingComplete = false;
    },
    onEnable(row) {
      // alert("Enable");
      this.showEnable = true;
      this.expirationOpen = false;
      this.row = row;
    },
    enableCoupon() {
      let self = this;
      let expireIn =
        self.expiration === ""
          ? 0
          : parseInt(new Date(self.expiration).getTime() / 1000);

      collections.salesManParams
        .doc(self.row.id)
        .update({
          expireIn: expireIn,
        })
        .then(() => {
          self.isButtonLoadingComplete = true;
          self.expiration = "";
          self.data.some((item) => {
            if (item.id === self.row.id) {
              item.expireIn = expireIn;
            }
          });
          self.row = null;
          self.showEnable = false;
        });

      self.isButtonLoadingComplete = false;
    },
    onCopySuccess() {
      this.$notify({
        message: "Copy Success!",
        type: "success",
      });
    },
    onCopyError() {
      this.$notify({
        message: "Copy Failed!",
        type: "error",
      });
    },
    initName(name) {
      if (this.$tools.isNull(name)) {
        return "no name";
      } else {
        return name;
      }
    },
    initEmail(data) {
      let self = this;
      let email = "no email";

      if (self.$tools.isNull(data)) {
        return "no email";
      } else {
        return email;
      }
    },
    initTime(time) {
      if (time == 0) {
        return "0 month";
      } else if (time == 1) {
        return "1 month";
      } else {
        return time + " months";
      }
    },
    initExpiration(expireIn) {
      if (this.$tools.isNull(expireIn) || expireIn === 0) {
        return "2099/1/1";
      } else {
        let timestamp = expireIn * 1000;
        return this.$tools.formatYYMMDD(timestamp);
      }
    },
    isNull(obj) {
      return this.$tools.isNull(obj);
    },
  },
};
</script>
