  color: #4dbdf7 !important;
}
.tk-dynamic-link-opertation .fa-inbox:hover {
  color: #32325d !important;
}
.tk-dynamic-link-opertation .fa-copy:hover {
  color: #2dce89 !important;
}
.popper__arrow {
  display: none !important;
